<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row>
        <el-col :span="3">
          <el-dropdown split-button type="primary"  @click="handleClick" @command="handleCommand">
            {{cycle}}
            <el-dropdown-menu slot="dropdown"  >
              <el-dropdown-item command="day"></el-dropdown-item>
              <el-dropdown-item command="week"></el-dropdown-item>
              <el-dropdown-item command="month"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
        <el-col :span="3">
          <el-switch
            width= "100"
            v-model="userType"
            active-value="1"
            inactive-value="0"
            on-text=""
            off-text=""
            @change="reSearch()"
          >
          </el-switch>
        </el-col>
        <el-col :span="12">
          <div class="chooseDate" style="float:right;">
            <el-date-picker
              v-model="searchKey"
              type="daterange"
              align="right"
              unlink-panels
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              :picker-options="pickerOptions2"
              @change="search($event)">
            </el-date-picker>
          </div>
        </el-col>  
      </el-row>
    </h3>
    <div slot="body">
        <el-table
        :data="tableData.rows"
        border
        style="width: 100%"
        v-loading="listLoading"
        @selection-change="handleSelectionChange"
        @cell-click="gonext"
        @header-click="selectTongJi">
        <!--checkbox IE https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="id"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          prop="usernum"
          label="" 
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="callnum"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="callsuccessnum2"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="callfailnum2"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="answersuccessnum2"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="answerfailnum2"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="chatsuccessnum2"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="chatfailnum2"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="auviyes2"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="auvino2"
          label=""
         >
        </el-table-column>
             <el-table-column
          prop="duration"
          label=""
         >
        </el-table-column>
        <el-table-column
          prop="createdAt"
          label=""
         >
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="tableData.pagination.pageNo"
        :page-sizes="[50,100,150]"
        :page-size="tableData.pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.pagination.total">
      </el-pagination>
      <div class="description"><p></p></div>
      <div id="myChart" :style="{width: '100%', height: '600px'}"></div>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        cycle: '',
        command: 'day',
        userType:true,
        showDate: '',
        showRate: '', 
        showCol: '1',
        currentRow: {},
        myChart: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: [new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 10), new Date()],
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        },
        pickerOptions2: {
          shortcuts: [
          {
            text: '7',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '15',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 15);
              picker.$emit('pick', [start, end]);
            }
          },{
            text: '30',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
      }
    },
    methods: {
      handleSelectionChange(val){
      },
      handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      reSearch() {
        this.loadData();
      },
      search(target){
        this.loadData();
      },
      gonext(row, val, cell, event) {
        if ( val.label == "") {
          this.$router.push({path: '/tongji/userActivity',query:{date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/7',query:{date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/7',query:{date:row.createdAt,type:'success',userType:this.userType}})
        }
        if ( val.label == "") {
           this.$router.push({path: '/tongji/16',query:{date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/countAnswerDetail',query:{date:row.createdAt,total:row.answersuccessnum,userType:this.userType}});
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/5',query: {date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/8',query:{date:row.createdAt,total:row.chatsuccessnum,userType:this.userType}});
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/countChatFailDetail',query:{date:row.createdAt,total:row.chatfailnum,userType:this.userType}});        
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/8',query:{date:row.createdAt,total:row.auviyes,type:'success',userType:this.userType}});          
        }
        if ( val.label == "") {
          this.$router.push({path: '/tongji/6',query:{userType:this.userType}});
        }	
      },
      selectTongJi(val) {
        if ( val.label == "") {
          this.showCol = 1;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol = 2;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol = 3;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol = 4;
          this.loadData(); 
        }
        if ( val.label == "") {
          this.showCol = 5;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol = 6;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol = 7;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol = 8;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol = 9;
          this.loadData();
        }
        if ( val.label == "") {
          this.showCol= 10;
          this.loadData();
        }	
      },
      handleCommand(command) {
        if( command == "day") {
          this.cycle="";
        };
        if( command == "week") {
          this.cycle="";
        };
        if( command == "month") {
          this.cycle="";
        };
        this.command = command;
        this.loadData();
      },
      loadData(){
          tongjiApi.CountCallanswerchat({
            key: this.searchKey,
            userType: this.userType,
            type: this.command,
            pageSize: this.tableData.pagination.pageSize,
            pageNo: this.tableData.pagination.pageNo
          })
          .then(res => {
            const e = this;
            this.tableData.rows = res.records;
            this.tableData.pagination.total = res.total;
            //x
            let dx1 = this.$moment(this.searchKey[0]).format('YYYY-MM-DD');
            let dx2 = this.$moment(this.searchKey[1]).format('YYYY-MM-DD');
            let days = (this.searchKey[1]-this.searchKey[0])/(24*60*60*1000);
            let dx = [];
            let userNum = [];
            let callNum = [];
            let callSuccessNum = [];
            let callSuccessRate = [];
            let callFailNum = [];
            let callFailRate = [];
            let answerNum = [];
            let answerNumRate2 = [];
            let answerFailNum = [];
            let answerFailNumRate2 = [];
            let chatNum = [];
            let chatNumRate2 = []; 
            let chatFailNum = [];
            let chatFailNumRate2 = [];
            let auviyes = [];
            let auviyesRate2 = [];
            let auvino = [];
            let auvinoRate2 = [];

            let vo = [];
            for ( var i=0; i<=days ;i++ )  {
              let d = this.$moment(dx1).add(i, 'days').format('YYYY-MM-DD');
              dx.push(d);
            }
            for ( let index in res.records ) {
              let x = dx.indexOf(res.records[index].createdAt);
              if ( x != -1 ) {
                userNum[x] = parseInt(res.records[index].usernum);
                callNum[x] = parseInt(res.records[index].callnum);
                callSuccessNum[x] = parseInt(res.records[index].callsuccessnum);
                callSuccessRate[x] = parseInt(res.records[index].callSuccessRate);
                callFailNum[x] = parseInt(res.records[index].callfailnum);
                callFailRate[x] = parseInt(res.records[index].callFailRate);

                answerNum[x] = parseInt(res.records[index].answersuccessnum);
                answerNumRate2[x] = parseInt(res.records[index].answerSuccessRate2);

                answerFailNum[x] = parseInt(res.records[index].answerfailnum);
                answerFailNumRate2[x] = parseInt(res.records[index].answerFailRate2);

                chatNum[x] = parseInt(res.records[index].chatsuccessnum);
                chatNumRate2[x] = parseInt(res.records[index].chatSuccessRate2);

                chatFailNum[x] = parseInt(res.records[index].chatfailnum);
                chatFailNumRate2[x] = parseInt(res.records[index].chatFailRate2);

                auviyes[x] = parseInt(res.records[index].auviyes);
                auviyesRate2[x] = parseInt(res.records[index].auviyesRate2);

                auvino[x] = parseInt(res.records[index].auvino);
                auvinoRate2[x] = parseInt(res.records[index].auvinoRate2);

              }
            }
            console.log()
            //
            this.myChart = this.$echarts.init(document.getElementById('myChart'));
            if (this.showCol == 1) {
              var option = {
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = [''];
            };
            if (this.showCol == 2) {
              var option = {
                    '': false,
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = [''];
            };
            if (this.showCol == 3) {
              var option = {
                    '': false,
                    '': false,
                    '': true,
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = ['',''];
              
            };
            if (this.showCol == 4) {
              var option = {
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': true,
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = ['',''];

            };
            if (this.showCol == 5) {
              var option = {
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': true,
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = ['',''];

            };
            if (this.showCol == 6) {
              var option = {
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': true,
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = ['',''];
            };
            if (this.showCol == 7) {
              var option = {
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': true,
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = ['',''];              
            };
            if (this.showCol == 8) {
              var option = {
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': true,
                    '': true,
                    '': false,
                    '': false,
                    '': false,
                    '': false
              };
              var legendData = ['',''];              
            };
            if (this.showCol == 9) {
              var option = {
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': true,
                    '': true,
                    '': false,
                    '': false
              };
              var legendData = ['',''];              
            };
            if (this.showCol == 10) {
              var option = {
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': false,
                    '': true,
                    '': true
              };
              var legendData = ['',''];              
            };
            // 
            this.myChart.setOption({
              tooltip: {
                  trigger: 'axis'
              },
              legend: {
                  data: legendData,
                  selected: option,
              },
              grid: {
                  left: '3%',
                  right: '4%',
                  bottom: '3%',
                  containLabel: true
              },
              toolbox: {
                  feature: {
                      saveAsImage: {}
                  }
              },
              xAxis: {
                  type: 'category',
                  boundaryGap: false,
                  data: dx
              },
              yAxis: [
                {
                  type: 'value',
                  name: ''
                },
                {
                  type: 'value',
                  name: '',
                  axisLabel: {
                    formatter: '{value} %'
                  }
                }
              ],
              series: [
                 {
                      name:'',
                      type:'line',
                      stack: '1',
                      data:userNum
                  },
                 {
                      name:'',
                      type:'line',
                      stack: '2',
                      data:callNum
                  },
                  {
                      name:'',
                      type:'line',
                      stack: '3',
                      data:callSuccessNum
                  },
                  {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '33',
                      data:callSuccessRate
                  },
                   {
                      name:'',
                      type:'line',
                      stack: '4',
                      data:callFailNum
                  },
                  {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '44',
                      data:callFailRate
                  },
                  {
                      name:'',
                      type:'line',
                      stack: '5',
                      data:answerNum
                  },
                   {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '55',
                      data:answerNumRate2
                  },
                  {
