                      type:'line',
                      yAxisIndex: 1,
                      stack: '66',
                      data:answerFailNumRate2
                  },
                  {
                      name:'',
                      type:'line',
                      stack: '7',
                      data:chatNum
                  },
                  {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '77',
                      data:chatNumRate2
                  },
                  {
                      name:'',
                      type:'line',
                      stack: '8',
                      data:chatFailNum
                  },
                  {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '88',
                      data:chatFailNumRate2
                  },
                   {
                      name:'',
                      type:'line',
                      stack: '9',
                      data:auviyes
                  },
                  {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '99',
                      data:auviyesRate2
                  },
                  {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '10',
                      data:auvino
                  },
                  {
                      name:'',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '11',
                      data:auvinoRate2
                  }
              ]
            });
            var go = this.$router;
            //
            myChart.on('click', function (params) {
              alert(params.seriesName);
              let date = params.name;
              let value = params.value;
              if ( params.seriesName == '' ) {
                go.push({path: '/tongji/userActivity'});
              }
              if ( params.seriesName =='') {
                go.push({path: '/tongji/7',query:{dateKey:date}});
              }
              if ( params.seriesName == '' ) {
                go.push({path: ''});
              }
              if ( params.seriesName == '' ) {
                go.push({path: '/tongji/16'});
              }
              if ( params.seriesName == '' ) {
                go.push({path: ''});
              }
              if ( params.seriesName == '' ) {
                go.push({path: '/tongji/5'});
              }
              if ( params.seriesName == '' ) {
                go.push({path: '/tongji/8'});
              }
              if ( params.seriesName == '' ) {
                go.push({path: '/tongji/8'});
              }
              if ( params.seriesName == '' ) {
                go.push({path: '/tongji/6'});
              }
              if ( params.seriesName == '' ) {
                go.push({path: '/tongji/6'});
              }
              
            });
          });
      }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
  #myChart {
    margin-top:50px;
  }
  .description{
   margin-top: 60px;
   font-size:12px;
   color:#333;
  }
</style>



